@import 'bulma';
@import 'bulma-slider';

*{
  box-sizing: border-box;
}

html, #root {
  height: 100%;
  width: 100%;
}

body {
  background-color: #111111;
}


html,
body,
a,
button {
  cursor: none !important;
}

.underline {
  text-decoration: underline;
}

.twitter-picker {
  margin: 20px 0;
  width: 100% !important;
}